/* Container styling */
.julekalender {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 15px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff8e7;
  border: 5px solid #ff4f00;
  border-radius: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  background-image: url("https://www.transparenttextures.com/patterns/snowflakes.png");
  background-size: cover;
  background-position: center;
  margin-top: 60px;
  width: 72vw;
}

/* Each door container with festive light border */
.door-container {
  position: relative;
  width: 10vw;
  height: 10vw;
  perspective: 1000px;
  cursor: pointer;
  background-color: #ffcccb;
  border-radius: 10px;
  border: 3px solid #d2691e;
  box-shadow: 0 0 5px #d2691e, inset 0 0 10px #f1c40f;
  overflow: hidden;
}

/* Stationary content behind the door */
.door-content {
  position: absolute;
  width: 90%;
  height: 90%;
  background-color: #4caf50;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  text-align: center;
  padding: 10px;
  border-radius: 8px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 1;
  overflow: hidden;
}

/* Door with festive wooden style */
.door {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #8b4513;
  background-image: linear-gradient(145deg, #8b4513 50%, #5e3210 50%);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  border: 3px solid #543d25;
  border-radius: 10px;
  transform-origin: left;
  transform-style: preserve-3d;
  transition: transform 0.6s ease-in-out;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3), inset 0 0 10px #ffd700;
  z-index: 2;
}

.door.open {
  transform: rotateY(-120deg);
}

/* Day number styling */
.day-number {
  font-size: 24px;
  color: #ffeb3b;
  text-shadow: 1px 1px 2px #000;
}

/* Snow and festive look */
.julekalender {
  background-image: url("https://www.transparenttextures.com/patterns/stardust.png"),
    url("https://www.transparenttextures.com/patterns/snow.png");
  background-size: cover;
  border: 4px solid #d2691e;
  padding: 10px;
  border-radius: 10px;
}

.door-content {
  font-size: 14px;
  padding: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  background: radial-gradient(circle, #a8e063, #56ab2f);
}
